'use client';

import { useState, useEffect } from 'react';
import { teamMembers } from '../data/teamData';
import Link from 'next/link';

type TicketStatus = 'open' | 'closed';
type TaskLocation = 'on-site' | 'remote';

interface Ticket {
  id: string;
  ticketNumber: string;
  createdAt: string;
  status: TicketStatus;
  issue: string;
  resolution: string;
  location: TaskLocation;
  closedAt?: string;
  responseTimeMinutes?: number;
}

interface MemberTickets {
  memberId: string;
  memberName: string;
  memberRole: string;
  initials: string;
  tickets: Ticket[];
}

function getInitials(name: string): string {
  return name
    .split(' ')
    .map(part => part.charAt(0).toUpperCase())
    .join('');
}

function generateTicketNumber(initials: string, existingTickets: Ticket[]): string {
  const today = new Date();
  const dateStr = `${today.getFullYear()}${String(today.getMonth() + 1).padStart(2, '0')}${String(today.getDate()).padStart(2, '0')}`;
  const todayTickets = existingTickets.filter(t => t.ticketNumber.includes(dateStr));
  const sequence = String(todayTickets.length + 1).padStart(3, '0');
  return `${initials}-${dateStr}-${sequence}`;
}

export default function AdminPage() {
  const [allMemberTickets, setAllMemberTickets] = useState<MemberTickets[]>([]);
  const [showCreateForm, setShowCreateForm] = useState(false);
  const [selectedMemberId, setSelectedMemberId] = useState<string>('');
  const [filterMember, setFilterMember] = useState<string>('all');
  const [filterStatus, setFilterStatus] = useState<'all' | 'open' | 'closed'>('all');
  
  const [newTicketData, setNewTicketData] = useState({
    issue: '',
    location: 'remote' as TaskLocation
  });

  // Load all tickets from localStorage
  useEffect(() => {
    const loadAllTickets = () => {
      const memberTickets: MemberTickets[] = teamMembers.map(member => {
        const stored = localStorage.getItem(`tickets-${member.id}`);
        const tickets = stored ? JSON.parse(stored) : [];
        return {
          memberId: member.id,
          memberName: member.name,
          memberRole: member.role,
          initials: getInitials(member.name),
          tickets
        };
      });
      setAllMemberTickets(memberTickets);
    };

    loadAllTickets();
    
    // Refresh every 5 seconds to catch updates from member pages
    const interval = setInterval(loadAllTickets, 5000);
    return () => clearInterval(interval);
  }, []);

  // Save tickets for a specific member
  const saveTicketsForMember = (memberId: string, tickets: Ticket[]) => {
    localStorage.setItem(`tickets-${memberId}`, JSON.stringify(tickets));
    setAllMemberTickets(prev => prev.map(mt => 
      mt.memberId === memberId ? { ...mt, tickets } : mt
    ));
  };

  // Create ticket for a member
  const handleCreateTicket = (e: React.FormEvent) => {
    e.preventDefault();
    if (!selectedMemberId || !newTicketData.issue.trim()) return;

    const memberData = allMemberTickets.find(mt => mt.memberId === selectedMemberId);
    if (!memberData) return;

    const newTicket: Ticket = {
      id: Date.now().toString(),
      ticketNumber: generateTicketNumber(memberData.initials, memberData.tickets),
      createdAt: new Date().toISOString(),
      status: 'open',
      issue: newTicketData.issue.trim(),
      resolution: '',
      location: newTicketData.location
    };

    saveTicketsForMember(selectedMemberId, [newTicket, ...memberData.tickets]);
    setNewTicketData({ issue: '', location: 'remote' });
    setSelectedMemberId('');
    setShowCreateForm(false);
  };

  // Get all tickets flattened with member info
  const getAllTickets = () => {
    return allMemberTickets.flatMap(mt => 
      mt.tickets.map(ticket => ({
        ...ticket,
        memberId: mt.memberId,
        memberName: mt.memberName,
        memberRole: mt.memberRole,
        initials: mt.initials
      }))
    ).sort((a, b) => new Date(b.createdAt).getTime() - new Date(a.createdAt).getTime());
  };

  const allTickets = getAllTickets();
  
  // Apply filters
  const filteredTickets = allTickets.filter(ticket => {
    if (filterMember !== 'all' && ticket.memberId !== filterMember) return false;
    if (filterStatus !== 'all' && ticket.status !== filterStatus) return false;
    return true;
  });

  // Stats
  const totalOpen = allTickets.filter(t => t.status === 'open').length;
  const totalClosed = allTickets.filter(t => t.status === 'closed').length;
  const totalOnSite = allTickets.filter(t => t.location === 'on-site').length;
  const totalRemote = allTickets.filter(t => t.location === 'remote').length;

  const getAvatarGradient = (name: string) => {
    const colors = [
      'from-cyan-400 to-blue-500',
      'from-violet-400 to-purple-500',
      'from-rose-400 to-pink-500',
      'from-amber-400 to-orange-500',
      'from-emerald-400 to-teal-500',
    ];
    const index = name.charCodeAt(0) % colors.length;
    return colors[index];
  };

  return (
    <div className="min-h-screen bg-slate-950 bg-grid-pattern bg-radial-gradient">
      {/* Header */}
      <header className="sticky top-0 z-40 glass border-b border-slate-700/50">
        <div className="max-w-7xl mx-auto px-6 py-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center gap-4">
              <Link 
                href="/"
                className="w-10 h-10 rounded-xl bg-slate-800 border border-slate-700 flex items-center justify-center text-slate-400 hover:text-white hover:bg-slate-700 transition-colors"
              >
                <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
                </svg>
              </Link>
              <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-rose-500 to-orange-600 flex items-center justify-center shadow-lg">
                <svg className="w-6 h-6 text-white" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M10.325 4.317c.426-1.756 2.924-1.756 3.35 0a1.724 1.724 0 002.573 1.066c1.543-.94 3.31.826 2.37 2.37a1.724 1.724 0 001.065 2.572c1.756.426 1.756 2.924 0 3.35a1.724 1.724 0 00-1.066 2.573c.94 1.543-.826 3.31-2.37 2.37a1.724 1.724 0 00-2.572 1.065c-.426 1.756-2.924 1.756-3.35 0a1.724 1.724 0 00-2.573-1.066c-1.543.94-3.31-.826-2.37-2.37a1.724 1.724 0 00-1.065-2.572c-1.756-.426-1.756-2.924 0-3.35a1.724 1.724 0 001.066-2.573c-.94-1.543.826-3.31 2.37-2.37.996.608 2.296.07 2.572-1.065z" />
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                </svg>
              </div>
              <div>
                <h1 className="text-xl font-bold text-white">Admin Dashboard</h1>
                <p className="text-sm text-slate-400">Ticket Management</p>
              </div>
            </div>
            
            <button
              onClick={() => setShowCreateForm(true)}
              className="flex items-center gap-2 px-4 py-2 rounded-xl bg-gradient-to-r from-cyan-500 to-blue-600 text-white font-medium shadow-lg hover:shadow-cyan-500/25 transition-all"
            >
              <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" />
              </svg>
              Create Ticket
            </button>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-6 py-8">
        {/* Stats Overview */}
        <section className="mb-8 animate-fade-in">
          <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
            <div className="p-5 rounded-2xl bg-slate-800/50 border border-slate-700/50">
              <p className="text-xs text-slate-500 mb-1">Total Tickets</p>
              <p className="text-3xl font-bold text-white">{allTickets.length}</p>
            </div>
            <div className="p-5 rounded-2xl bg-amber-500/10 border border-amber-500/30">
              <p className="text-xs text-amber-400 mb-1">Open</p>
              <p className="text-3xl font-bold text-amber-400">{totalOpen}</p>
            </div>
            <div className="p-5 rounded-2xl bg-emerald-500/10 border border-emerald-500/30">
              <p className="text-xs text-emerald-400 mb-1">Closed</p>
              <p className="text-3xl font-bold text-emerald-400">{totalClosed}</p>
            </div>
            <div className="p-5 rounded-2xl bg-slate-800/50 border border-slate-700/50">
              <p className="text-xs text-slate-500 mb-1">Location Split</p>
              <p className="text-lg font-bold">
                <span className="text-cyan-400">{totalOnSite}</span>
                <span className="text-slate-600 mx-2">On-Site</span>
                <span className="text-violet-400">{totalRemote}</span>
                <span className="text-slate-600 ml-2">Remote</span>
              </p>
            </div>
          </div>
        </section>

        {/* Team Member Quick Stats */}
        <section className="mb-8">
          <h2 className="text-lg font-semibold text-white mb-4">Team Members</h2>
          <div className="grid grid-cols-2 md:grid-cols-5 gap-4">
            {allMemberTickets.map(mt => {
              const openCount = mt.tickets.filter(t => t.status === 'open').length;
              const closedCount = mt.tickets.filter(t => t.status === 'closed').length;
              return (
                <Link
                  key={mt.memberId}
                  href={`/member/${mt.memberId}`}
                  className="p-4 rounded-xl bg-slate-800/40 border border-slate-700/50 hover:bg-slate-800/60 hover:border-slate-600/50 transition-all group"
                >
                  <div className="flex items-center gap-3 mb-3">
                    <div className={`w-10 h-10 rounded-lg bg-gradient-to-br ${getAvatarGradient(mt.memberName)} flex items-center justify-center text-white font-bold text-sm`}>
                      {mt.initials}
                    </div>
                    <div className="flex-1 min-w-0">
                      <p className="text-sm font-medium text-white truncate group-hover:text-cyan-400 transition-colors">
                        {mt.memberName.split(' ')[0]}
                      </p>
                      <p className="text-xs text-slate-500 truncate">{mt.memberRole}</p>
                    </div>
                  </div>
                  <div className="flex items-center gap-2 text-xs">
                    <span className="text-amber-400">{openCount} open</span>
                    <span className="text-slate-600">‚Ä¢</span>
                    <span className="text-emerald-400">{closedCount} closed</span>
                  </div>
                </Link>
              );
            })}
          </div>
        </section>

        {/* Filters */}
        <section className="mb-6">
          <div className="flex flex-wrap items-center gap-4">
            <div>
              <label className="block text-xs text-slate-500 mb-1">Filter by Member</label>
              <select
                value={filterMember}
                onChange={(e) => setFilterMember(e.target.value)}
                className="px-4 py-2 rounded-xl bg-slate-800 border border-slate-700 text-white text-sm focus:border-cyan-500 outline-none"
              >
                <option value="all">All Members</option>
                {teamMembers.map(member => (
                  <option key={member.id} value={member.id}>{member.name}</option>
                ))}
              </select>
            </div>
            <div>
              <label className="block text-xs text-slate-500 mb-1">Filter by Status</label>
              <select
                value={filterStatus}
                onChange={(e) => setFilterStatus(e.target.value as 'all' | 'open' | 'closed')}
                className="px-4 py-2 rounded-xl bg-slate-800 border border-slate-700 text-white text-sm focus:border-cyan-500 outline-none"
              >
                <option value="all">All Status</option>
                <option value="open">Open Only</option>
                <option value="closed">Closed Only</option>
              </select>
            </div>
            <div className="ml-auto text-sm text-slate-400">
              Showing {filteredTickets.length} of {allTickets.length} tickets
            </div>
          </div>
        </section>

        {/* All Tickets List */}
        <section>
          <h2 className="text-lg font-semibold text-white mb-4">All Tickets</h2>
          
          {filteredTickets.length === 0 ? (
            <div className="text-center py-12 rounded-2xl bg-slate-800/30 border border-slate-700/30">
              <svg className="w-12 h-12 mx-auto text-slate-600 mb-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={1.5} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
              </svg>
              <p className="text-slate-500">No tickets found.</p>
            </div>
          ) : (
            <div className="space-y-3">
              {filteredTickets.map((ticket, index) => (
                <div 
                  key={ticket.id} 
                  className={`p-4 rounded-xl border animate-fade-in ${
                    ticket.status === 'open' 
                      ? 'bg-slate-800/40 border-amber-500/30' 
                      : 'bg-slate-800/30 border-slate-700/50'
                  }`}
                  style={{ animationDelay: `${index * 30}ms` }}
                >
                  <div className="flex items-start gap-4">
                    {/* Member Avatar */}
                    <div className={`shrink-0 w-10 h-10 rounded-lg bg-gradient-to-br ${getAvatarGradient(ticket.memberName)} flex items-center justify-center text-white font-bold text-sm`}>
                      {ticket.initials}
                    </div>
                    
                    {/* Ticket Content */}
                    <div className="flex-1 min-w-0">
                      <div className="flex items-center flex-wrap gap-2 mb-2">
                        <span className={`px-2 py-0.5 rounded text-xs font-bold font-mono ${
                          ticket.status === 'open' ? 'bg-amber-500/20 text-amber-400' : 'bg-slate-700 text-slate-300'
                        }`}>
                          {ticket.ticketNumber}
                        </span>
                        <span className="text-xs text-slate-500">{ticket.memberName}</span>
                        <span className={`px-2 py-0.5 rounded text-xs ${
                          ticket.location === 'on-site' 
                            ? 'bg-cyan-500/20 text-cyan-400' 
                            : 'bg-violet-500/20 text-violet-400'
                        }`}>
                          {ticket.location === 'on-site' ? 'üìç On-Site' : 'üåê Remote'}
                        </span>
                        <span className={`px-2 py-0.5 rounded-full text-xs ${
                          ticket.status === 'open' 
                            ? 'bg-amber-500/20 text-amber-400' 
                            : 'bg-emerald-500/20 text-emerald-400'
                        }`}>
                          {ticket.status === 'open' ? 'Open' : 'Closed'}
                        </span>
                      </div>
                      
                      <p className="text-sm text-slate-300 mb-2">{ticket.issue}</p>
                      
                      {ticket.status === 'closed' && ticket.resolution && (
                        <div className="p-2 rounded-lg bg-emerald-500/10 border border-emerald-500/20 mt-2">
                          <p className="text-xs text-emerald-400 mb-1">Resolution:</p>
                          <p className="text-sm text-slate-300">{ticket.resolution}</p>
                        </div>
                      )}
                      
                      <p className="text-xs text-slate-600 mt-2">
                        Created: {new Date(ticket.createdAt).toLocaleDateString('en-ZA', { 
                          month: 'short', day: 'numeric', hour: '2-digit', minute: '2-digit'
                        })}
                        {ticket.closedAt && (
                          <> ‚Ä¢ Closed: {new Date(ticket.closedAt).toLocaleDateString('en-ZA', { 
                            month: 'short', day: 'numeric', hour: '2-digit', minute: '2-digit'
                          })}</>
                        )}
                        {ticket.responseTimeMinutes !== undefined && ticket.responseTimeMinutes > 0 && (
                          <> ‚Ä¢ Response: {ticket.responseTimeMinutes} min</>
                        )}
                      </p>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          )}
        </section>
      </main>

      {/* Create Ticket Modal */}
      {showCreateForm && (
        <div className="fixed inset-0 z-50 overflow-hidden">
          <div 
            className="absolute inset-0 bg-black/70 backdrop-blur-sm animate-fade-in"
            onClick={() => setShowCreateForm(false)}
          />
          
          <div className="absolute inset-0 flex items-center justify-center p-6">
            <div className="w-full max-w-lg bg-slate-900 rounded-2xl border border-slate-700/50 shadow-2xl animate-fade-in">
              <div className="p-6 border-b border-slate-700/50">
                <div className="flex items-center justify-between">
                  <h2 className="text-xl font-bold text-white">Create Ticket for Team Member</h2>
                  <button 
                    onClick={() => setShowCreateForm(false)}
                    className="w-8 h-8 rounded-lg bg-slate-800 flex items-center justify-center text-slate-400 hover:text-white transition-colors"
                  >
                    <svg className="w-5 h-5" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M6 18L18 6M6 6l12 12" />
                    </svg>
                  </button>
                </div>
              </div>
              
              <form onSubmit={handleCreateTicket} className="p-6 space-y-5">
                {/* Select Member */}
                <div>
                  <label className="block text-sm font-medium text-slate-300 mb-2">
                    Assign to Team Member <span className="text-rose-400">*</span>
                  </label>
                  <select
                    value={selectedMemberId}
                    onChange={(e) => setSelectedMemberId(e.target.value)}
                    required
                    className="w-full px-4 py-3 rounded-xl bg-slate-800 border border-slate-700 text-white focus:border-cyan-500 outline-none"
                  >
                    <option value="">Select a team member...</option>
                    {teamMembers.map(member => (
                      <option key={member.id} value={member.id}>
                        {member.name} ({member.role})
                      </option>
                    ))}
                  </select>
                </div>

                {/* Preview Ticket Number */}
                {selectedMemberId && (
                  <div className="p-3 rounded-xl bg-slate-800/50 border border-slate-700/30">
                    <p className="text-xs text-slate-500 mb-1">Ticket Number (auto-generated)</p>
                    <p className="text-sm font-mono text-cyan-400">
                      {generateTicketNumber(
                        getInitials(teamMembers.find(m => m.id === selectedMemberId)?.name || ''),
                        allMemberTickets.find(mt => mt.memberId === selectedMemberId)?.tickets || []
                      )}
                    </p>
                  </div>
                )}

                {/* Date Display */}
                <div>
                  <label className="block text-sm font-medium text-slate-300 mb-2">Date</label>
                  <div className="px-4 py-3 rounded-xl bg-slate-800/50 border border-slate-700/50 text-slate-400">
                    {new Date().toLocaleDateString('en-ZA', { weekday: 'long', year: 'numeric', month: 'long', day: 'numeric' })}
                  </div>
                </div>

                {/* Task Location */}
                <div>
                  <label className="block text-sm font-medium text-slate-300 mb-2">Task Location</label>
                  <div className="flex gap-3">
                    <button
                      type="button"
                      onClick={() => setNewTicketData({ ...newTicketData, location: 'on-site' })}
                      className={`flex-1 px-4 py-3 rounded-xl border transition-all ${
                        newTicketData.location === 'on-site'
                          ? 'bg-cyan-500/20 border-cyan-500 text-cyan-400'
                          : 'bg-slate-800 border-slate-700 text-slate-400 hover:border-slate-600'
                      }`}
                    >
                      üìç On-Site
                    </button>
                    <button
                      type="button"
                      onClick={() => setNewTicketData({ ...newTicketData, location: 'remote' })}
                      className={`flex-1 px-4 py-3 rounded-xl border transition-all ${
                        newTicketData.location === 'remote'
                          ? 'bg-violet-500/20 border-violet-500 text-violet-400'
                          : 'bg-slate-800 border-slate-700 text-slate-400 hover:border-slate-600'
                      }`}
                    >
                      üåê Remote
                    </button>
                  </div>
                </div>

                {/* Issue Description */}
                <div>
                  <label className="block text-sm font-medium text-slate-300 mb-2">
                    Issue Description <span className="text-rose-400">*</span>
                  </label>
                  <textarea
                    value={newTicketData.issue}
                    onChange={(e) => setNewTicketData({ ...newTicketData, issue: e.target.value })}
                    rows={4}
                    required
                    className="w-full px-4 py-3 rounded-xl bg-slate-800 border border-slate-700 text-white focus:border-cyan-500 focus:ring-1 focus:ring-cyan-500 outline-none transition-colors resize-none"
                    placeholder="Describe the issue or task..."
                  />
                </div>

                <div className="flex gap-3 pt-2">
                  <button
                    type="submit"
                    disabled={!selectedMemberId || !newTicketData.issue.trim()}
                    className="flex-1 px-5 py-3 rounded-xl bg-gradient-to-r from-emerald-500 to-teal-600 text-white font-medium shadow-lg hover:shadow-emerald-500/25 transition-all disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    Create Ticket
                  </button>
                  <button
                    type="button"
                    onClick={() => setShowCreateForm(false)}
                    className="px-5 py-3 rounded-xl bg-slate-700 text-slate-300 font-medium hover:bg-slate-600 transition-colors"
                  >
                    Cancel
                  </button>
                </div>
              </form>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

